<template>
  <div :class="$vuetify.breakpoint.xsOnly ? 'py-2' : 'px-2 py-4'">
    <div>
      <h3 style="font-family: Nunito;" class="px-3">{{ titulo }}
        <span v-if="verMas" class="caption font-weight-bold primary--text" @click="verMas()" style="cursor: pointer">Ver ms</span>
      </h3>
    </div>
    <section :key="numChildren + optionsStr + $vuetify.breakpoint.xsOnly" ref="splide" class="splide"
             aria-label="Carrusel" :data-splide="optionsStr">
      <div class="splide__track">
        <ul class="splide__list">
          <slot/>
        </ul>
      </div>
    </section>
  </div>
</template>

<script>
  import Splide from '@splidejs/splide';
  import '@splidejs/splide/dist/css/splide.min.css';

  export default {
    name: "Carrusel",
    props: {
      elementos: {type: Number, default: 0},
      titulo: {type: String, required: false},
      verMas: {type: Function},
      perPageEscritorio: {type: [Number, String], default: 2},
      autoplay: {type: Boolean}
    },
    data() {
      return {
        splide: null
      }
    },
    mounted() {
      this.splide = new Splide(this.$refs.splide).mount();
    },
    computed: {
      optionsStr() {
        const isEnoughSlides = getSlidesLength(this) > 1;
        const options = {
          type: 'loop',
          height: '100%',
          autoplay: this.autoplay,
          perPage: !this.$vuetify.breakpoint.xsOnly && isEnoughSlides ? this.perPageEscritorio : 1,
          arrows: isEnoughSlides,
          pagination: isEnoughSlides,
          gap: 8,
          classes: {
            pagination: 'splide__pagination ma-0 pa-0'
          }
        };
        return JSON.stringify(options);
      },
      numChildren () {
        return getSlidesLength(this);
      }
    },
    watch: {
      optionsStr () {
        refreshSplide(this);
      },
      numChildren () {
        refreshSplide(this);
      },
      '$vuetify.breakpoint.xsOnly' () {
        refreshSplide(this);
      }
    },
    beforeDestroy() {
      this.splide.destroy();
    }
  }

  function refreshSplide(vm) {
    if (vm.$refs.splide) {
      vm.$nextTick(() => {
        vm.splide.destroy();
        vm.splide = new Splide(vm.$refs.splide).mount();
      });
    }
  }

  function getSlidesLength(vm) {
    // Devuelve la lista de elementos hijos del carrusel Splide
    return vm.splide ? vm.splide.Components.Slides.getLength(true) : 0;
  }
</script>

<style scoped>
</style>
